import React, { useState, useEffect } from "react";
import Search from "./Search";
import CardPageSizeSelect from "./CardPageSizeSelect";
import Footer from "./Footer";
import TabExampleBasic from "./Tabs";
import { useHistory, Link } from "react-router-dom";
import { connect } from "react-redux";
import {
  Table,
  Popup,
  Icon,
  Button,
  Segment,
  Pagination,
  Responsive,
  Header,
  Card,
  Divider,
  Image
} from "semantic-ui-react";
import { searchChange } from "../reducers/searchReducer";
import { fetchData } from "../reducers/baseballReducer";
import { addFavorite } from "../reducers/userReducer";
import _ from "lodash";
var moment = require("moment");
moment().format();

const searchCards = ({ baseball, search }) => {
  return search
    ? baseball.filter(a =>
        a.title[0].toLowerCase().includes(search.toLowerCase())
      )
    : baseball;
};

const style = {
  borderRadius: 0,
  padding: "2em"
};

const Cards = props => {
  useEffect(() => {
    setData(props.cardsToShow);
  }, [props]);

  console.log("Logged User", props.loggedUser);

  const [column, setColumn] = useState(null);
  const [direction, setDirection] = useState(null);
  const [favorites, setFavorite] = useState("");
  const [filteredData, setData] = useState(props.cardsToShow);

  const handleSortNumeric = clickedColumn => {
    const sorter = data => parseInt(data[clickedColumn]);
    setData(_.sortBy(filteredData, sorter));
  };

  const handleSortReverse = () => {
    const sorter = data => parseInt(data);
    setData(_.sortBy(filteredData, sorter).reverse());
  };

  const handleSort = clickedColumn => {
    if (column !== clickedColumn) {
      setColumn(clickedColumn);
      console.log("Column", clickedColumn);
      if (clickedColumn === "title" || "acceptsOffers" || "timeStamp") {
        setData(_.sortBy(filteredData, [clickedColumn]));
        console.log("Filtered Data", filteredData);
      } else {
        handleSortNumeric(clickedColumn);
      }
      setDirection("ascending");
      console.log("Direction", direction);
      return;
    }

    if (clickedColumn === "title") {
      setData(_.sortBy(filteredData.reverse()));
    } else {
      handleSortReverse();
    }

    direction === "ascending"
      ? setDirection("descending")
      : setDirection("ascending");
  };

  const handlePagination = (e, pageInfo) => {
    props.fetchData(props.query, pageInfo.activePage, props.entries);
  };

  const removeFavorite = id => {
    setFavorite(favorites.filter(card => card !== id));
    console.log("FAVORITES AFTER REM", favorites);
  };

  let history = useHistory();

  const addFavorite = (card, user) => {
    if (typeof user.id === "undefined") {
      return history.push("/login");
    }
    props.addFavorite(card, user);
    if (favorites.includes(card.id)) {
      removeFavorite(card.id);
    } else {
      setFavorite([...favorites, card.id]);
    }
  };

  return (
    <>
      <Search />
      <TabExampleBasic />
      <Segment>
        <CardPageSizeSelect /> Total count: {props.pageOutput.totalEntries}
      </Segment>
      <Responsive maxWidth={767}>
        <strong>Click to Sort:</strong>
      </Responsive>
      <Table sortable celled fixed striped>
        <Table.Header>
          <Table.Row>
            <Table.HeaderCell
              textAlign="center"
              sorted={column === "title" ? direction : null}
              onClick={() => handleSort("title")}
            >
              Card Title
            </Table.HeaderCell>
            <Table.HeaderCell
              textAlign="center"
              width={2}
              sorted={column === "bidCount" ? direction : null}
              onClick={() => handleSort("bidCount")}
            >
              # Bids
            </Table.HeaderCell>
            <Table.HeaderCell
              textAlign="center"
              sorted={column === "watchCount" ? direction : null}
              onClick={() => handleSort("watchCount")}
            >
              Watchers
            </Table.HeaderCell>
            <Table.HeaderCell
              textAlign="center"
              sorted={column === "price" ? direction : null}
              onClick={() => handleSort("price")}
            >
              Price
            </Table.HeaderCell>
            <Table.HeaderCell
              textAlign="center"
              sorted={column === "timeStamp" ? direction : null}
              onClick={() => handleSort("timeStamp")}
            >
              Time Left
            </Table.HeaderCell>
            <Table.HeaderCell
              sorted={column === "acceptsOffers" ? direction : null}
              onClick={() => handleSort("acceptsOffers")}
              textAlign="center"
              width={2}
            >
              Auction Info
            </Table.HeaderCell>
            <Table.HeaderCell textAlign="center" width="2">
              Favorite
            </Table.HeaderCell>
          </Table.Row>
        </Table.Header>
        <Table.Body>
          {!filteredData
            ? "Sorry No Cards Found"
            : filteredData.map(card => (
                <>
                  <Responsive maxWidth={767}>
                    <>
                      <div className="ui piled compact segment">
                        <div className="ui card">
                          <div className="blurring dimmable image">
                            <div className="ui inverted dimmer">
                              <div className="content">
                                <div className="center">
                                  <div className="ui red button view">VIEW</div>
                                </div>
                              </div>
                            </div>
                            <Image
                              src={card.image}
                              href={card.itemURL}
                              centered
                              style={{ padding: "5px" }}
                            />
                          </div>
                          <div className="content">
                            <div
                              id="rate"
                              className="ui star rating right floated"
                              data-rating="3"
                            ></div>
                            <div className="header">
                              <a href={card.itemURL}>{card.title}</a>
                            </div>
                            <div
                              className="meta"
                              style={{ padding: "5px 0 0 0" }}
                            >
                              <span className="date">
                                <i className="clock icon"></i> Ends in{" "}
                                {moment(card.timeLeft).fromNow(true)}
                              </span>
                              <div style={{ padding: "10px 0 0 0" }}>
                                <span>
                                  <Button color="green">${card.price}</Button>
                                </span>
                                <span class="right floated date">
                                  {" "}
                                  <Button
                                    onClick={() =>
                                      addFavorite(card, props.loggedUser)
                                    }
                                    color={
                                      favorites.includes(card.id)
                                        ? "google plus"
                                        : "twitter"
                                    }
                                    icon={
                                      favorites.includes(card.id)
                                        ? "heart"
                                        : "heart outline"
                                    }
                                  />
                                </span>
                              </div>
                            </div>
                          </div>
                          <div className="extra content">
                            <div
                              className="ui right labeled button"
                              data-content="Bids"
                              data-variation="tiny"
                              tabindex="0"
                            >
                              <div className="ui blue icon tiny button">
                                <i className="gavel large icon"></i>
                              </div>
                              <a
                                href={card.itemURL}
                                className="ui basic blue left pointing label"
                              >
                                {card.bidCount}
                              </a>
                            </div>
                            <div
                              className="ui left labeled right floated button"
                              data-content="Watch Count"
                              data-variation="tiny"
                              tabindex="0"
                            >
                              <a
                                href={card.itemURL}
                                className="ui basic blue right pointing label"
                              >
                                {card.watchCount}
                              </a>
                              <div className="ui blue icon tiny button">
                                <i className="eye large icon"></i>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </>
                    {/* <Card style={{ width: "100%" }}>
                      <Card.Content>
                        <Card.Header
                          as="h3"
                          style={{ padding: "20px 15px 5px 10px" }}
                        >
                          <a href="{card.itemURL}">{card.title}</a>
                        </Card.Header>
                        <Image
                          src={card.image}
                          href={card.image}
                          centered
                          style={{ padding: "5px" }}
                        />
                      </Card.Content>
                      <Card.Content extra>
                        <div
                          className="ui right labeled button"
                          data-content="Like it!"
                          data-variation="tiny"
                          tabindex="0"
                        >
                          <div className="ui red icon tiny button">
                            <i className="thumbs outline up large icon"></i>
                          </div>
                          <a className="ui basic red left pointing label">
                            365
                          </a>
                        </div>
                        <div className="ui labeled icon menu">
                          <Icon name="dollar sign">${card.price}</Icon>
                          <Icon name="gavel">{card.bidCount} bids</Icon>
                          <Icon name="eye">{card.watchCount}</Icon>
                          <Icon name="clock">
                            {moment(card.timeLeft).fromNow(true)}
                          </Icon>
                        </div>
                      </Card.Content>
                      <Divider />
                    </Card> */}
                  </Responsive>
                  <Responsive as="tr" minWidth={768} style={{ width: "100%" }}>
                    <Popup
                      trigger={
                        <Table.Cell>
                          <a href={card.itemURL} target={"_blank"}>
                            {card.title}
                          </a>
                        </Table.Cell>
                      }
                      content={<img alt={card.title} src={card.image}></img>}
                      style={style}
                      position="left center"
                    ></Popup>
                    <Table.Cell textAlign="center">{card.bidCount}</Table.Cell>
                    <Table.Cell textAlign="center">
                      {card.watchCount}
                    </Table.Cell>
                    <Table.Cell textAlign="center">${card.price}</Table.Cell>
                    <Table.Cell textAlign="center">
                      {moment(card.timeLeft).fromNow(true)}
                    </Table.Cell>
                    <Table.Cell textAlign="center">
                      {card.acceptsOffers[0] === "true" ? (
                        <Popup
                          content="Accepts Offers"
                          trigger={<Icon color="green" name="check circle" />}
                        />
                      ) : null}
                      {card.listingType[0] === "Auction" ? (
                        <Popup
                          content="Auction"
                          trigger={<Icon color="blue" name="gavel" />}
                        />
                      ) : null}
                    </Table.Cell>
                    <Table.Cell textAlign="center">
                      <Button
                        onClick={() => addFavorite(card, props.loggedUser)}
                        color={
                          favorites.includes(card.id)
                            ? "google plus"
                            : "twitter"
                        }
                        icon={
                          favorites.includes(card.id)
                            ? "heart"
                            : "heart outline"
                        }
                      />
                    </Table.Cell>
                  </Responsive>
                </>
              ))}
        </Table.Body>
        <Table.Footer>
          <Table.Row>
            <Table.HeaderCell colSpan="7">
              {/* {/* <Button onClick={() => handlePagination(props.query, 2, 25)}>
                Go To Page 2
              </Button> */}
              <Pagination
                activePage={props.pageOutput.pageNumber}
                totalPages={Math.floor(
                  props.pageOutput.totalEntries / props.entries
                )}
                onPageChange={handlePagination}
              />
            </Table.HeaderCell>
          </Table.Row>
        </Table.Footer>
      </Table>
      <Footer />
    </>
  );
};

const mapStateToProps = state => {
  return {
    baseball: state.baseball,
    loggedUser: state.loggedUser,
    page: state.page,
    entries: state.entries,
    query: state.query,
    pageOutput: state.pageOutput,
    search: state.search,
    cardsToShow: searchCards(state)
  };
};

const mapDispatchToProps = {
  searchChange,
  fetchData,
  addFavorite
};

export default connect(mapStateToProps, mapDispatchToProps)(Cards);
